.root {
	position: relative;
}
.input {
	flex: 0 1 300px;
	border: 1px solid rgba(0, 0, 0, 0.1);
	padding: 8px 15px 8px 30px;
	border-radius: 15px;
	transition: all 0.3s ease 0s;
	&:focus {
		border: 1px solid rgba(0, 0, 0, 0.3);
	}
}
.icon__search {
	position: absolute;
	top: 50%;
	left: 5%;
	transform: translateY(-50%);
	max-width: 15px;
	opacity: 0.5;
}
.icon__close {
	position: absolute;
	top: 50%;
	right: 5%;
	transform: translateY(-50%);
	max-width: 15px;
	opacity: 0.5;
	cursor: pointer;
	transition: all 0.3s ease 0s;
	&:hover {
		opacity: 0.9;
	}
}
