@import "./variables";
@import "./libs/normalize";

@import "./components/all";

body {
	background-color: $background;
}

.wrapper {
	width: calc(100vw - 100px);
	height: 100%;
	background-color: #fff;
	margin: 50px auto;
	border-radius: 10px;
	max-width: 1400px;
	@media (max-width: 768px) {
		margin: 20px auto;
		width: calc(100vw - 40px);
	}
}

.content {
	padding: 40px 0;

	&__title {
		margin: 35px 0;
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 15px;
		justify-items: center;
		@media (max-width: 1400px) {
			grid-template-columns: repeat(3, 1fr);
		}
		@media (max-width: 1050px) {
			grid-template-columns: repeat(2, 1fr);
		}
		@media (max-width: 768px) {
			grid-template-columns: 1fr;
		}
	}

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@media (max-width: 1300px) {
			flex-direction: column-reverse;
		}
	}
}

.container {
	width: $container-width;
	margin: 0 auto;

	&--cart {
		max-width: 820px;
		margin: 90px auto;
		@media (max-width: 1200px) {
			margin: 50px auto;
		}
		@media (max-width: 992px) {
			margin: 20px auto;
		}
		@media (max-width: 768px) {
			margin: 10px auto;
		}
		.content__title {
			margin: 0;
		}
	}
}

.cart {
	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.content__title {
		display: flex;
		align-items: center;
		font-size: 32px;
		@media (max-width: 768px) {
			font-size: 24px;
		}

		svg {
			position: relative;
			top: -2px;
			width: 30px;
			height: 30px;
			margin-right: 10px;
			@media (max-width: 768px) {
				width: 20px;
				height: 20px;
			}
			path {
				stroke: $black;
				stroke-width: 1.9;
			}
		}
	}

	&__clear {
		display: flex;
		align-items: center;
		justify-content: space-between;
		column-gap: 5px;
		cursor: pointer;
		@include noselect();

		span {
			display: inline-block;
			color: #b6b6b6;
			font-size: 18px;
			white-space: nowrap;
			@media (max-width: 768px) {
				font-size: 14px;
			}
			@media (max-width: 500px) {
				font-size: 10px;
			}
		}

		span,
		svg,
		path {
			transition: all $duration ease-in-out;
		}

		&:hover {
			svg {
				path {
					stroke: darken($color: #b6b6b6, $amount: 50);
				}
			}
			span {
				color: darken($color: #b6b6b6, $amount: 50);
			}
		}
	}
	.content__items {
		display: block;
	}

	&__item {
		display: flex;
		width: 100%;
		border-top: 1px solid $gray-line;
		margin-top: 30px;
		@media (max-width: 500px) {
			flex-direction: column;
			align-items: center;
			row-gap: 15px;
		}

		&-img {
			display: flex;
			align-items: center;
			margin-right: 15px;
			width: 10%;
			@media (max-width: 500px) {
				width: auto;
				margin-right: 0;
			}

			img {
				width: 80px;
				height: 80px;
				@media (max-width: 768px) {
					width: 60px;
					height: 60px;
				}
			}
		}

		&-info {
			display: flex;
			flex-direction: column;
			justify-content: center;
			width: 40%;
			text-align: center;
			@media (max-width: 500px) {
				width: auto;
			}

			h3 {
				font-weight: bold;
				font-size: 22px;
				line-height: 27px;
				letter-spacing: 0.01em;
				white-space: nowrap;
				@media (max-width: 768px) {
					font-size: 16px;
				}
			}

			p {
				font-size: 18px;
				color: #8d8d8d;
				@media (max-width: 768px) {
					font-size: 14px;
				}
			}
		}

		&-count {
			display: flex;
			align-items: center;
			justify-content: space-between;
			gap: 15px;

			&-minus {
				svg {
					path:first-of-type {
						display: none;
					}
				}
			}

			b {
				font-size: 22px;
				@media (max-width: 768px) {
					font-size: 18px;
				}
			}
		}

		&-price {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 33%;
			@media (max-width: 500px) {
				width: auto;
			}

			b {
				font-weight: bold;
				font-size: 22px;
				letter-spacing: 0.01em;
				@media (max-width: 768px) {
					font-size: 18px;
				}
			}
		}

		&-remove {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			width: 4%;
			@media (max-width: 500px) {
				width: auto;
			}

			.button {
				border-color: darken($color: $gray-line, $amount: 10);
			}

			svg {
				transform: rotate(45deg);

				path {
					fill: darken($color: $gray-line, $amount: 15);
				}
			}

			.button {
				svg {
					width: 11.5px;
					height: 11.5px;
					position: relative;
				}
				&:hover,
				&:active {
					border-color: darken($color: $gray-line, $amount: 80);
					background-color: darken($color: $gray-line, $amount: 80);
				}
			}
		}
	}

	&__bottom {
		margin: 50px 0;
		@media (max-width: 768px) {
			margin: 20px 0;
		}

		&-details {
			display: flex;
			justify-content: space-between;
			@media (max-width: 410px) {
				flex-direction: column;
				align-items: center;
			}
			span {
				font-size: 22px;
				@media (max-width: 768px) {
					font-size: 16px;
				}

				&:last-of-type {
					b {
						color: $orange;
					}
				}
			}
		}

		&-buttons {
			display: flex;
			justify-content: space-between;
			margin-top: 40px;
			gap: 20px;

			@media (max-width: 768px) {
				margin-top: 20px;
			}
			@media (max-width: 410px) {
				flex-direction: column;
				align-items: center;
			}

			.go-back-btn {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 210px;
				@media (max-width: 768px) {
					width: 150px;
					padding: 8px;
				}

				border-color: darken($color: $gray-line, $amount: 10);

				span {
					color: darken($color: $gray-line, $amount: 20);
					font-weight: 500;
				}

				&:hover {
					background-color: darken($color: $gray-line, $amount: 90);
					border-color: darken($color: $gray-line, $amount: 90);

					span {
						color: $gray-line;
					}
				}

				svg {
					margin-right: 12px;
					path {
						fill: transparent;
						stroke-width: 2;
					}
				}
			}

			.pay-btn {
				display: flex;
				justify-content: center;
				align-items: center;
				font-size: 16px;
				font-weight: 600;
				width: 210px;
				padding: 16px;
				@media (max-width: 768px) {
					width: 150px;
					padding: 8px;
					font-size: 14px;
				}
			}
		}
	}

	&--empty {
		margin: 0 auto;
		width: 560px;
		text-align: center;

		h2 {
			font-size: 32px;
			margin-bottom: 10px;
		}

		p {
			font-size: 18px;
			line-height: 145.4%;
			letter-spacing: 0.01em;
			color: #777777;
		}

		icon {
			position: relative;
			top: 2px;
		}

		img {
			display: block;
			width: 300px;
			margin: 45px auto 60px;
		}

		.button--black {
			padding: 12px 0 14px;
			width: 230px;
			margin: 0 auto;
			font-weight: 600;
			font-size: 18px;
		}
	}
}
