.pagination {
	width: 30%;
	margin-inline: auto;
	@media (max-width: 992px) {
		width: 60%;
	}

	:global {
		.selected {
			background-color: #fe5f1e;
			a {
				color: #fff;
			}
		}
	}

	&__body {
		display: flex;
		justify-content: space-between;
		li {
			flex: 0 0 40px;
			height: 40px;
			border-radius: 50%;
			transition: all 0.3s ease 0s;
			@media (max-width: 500px) {
				flex: 0 0 25px;
				height: 25px;
			}
			a {
				display: flex;
				justify-content: center;
				align-items: center;
				width: 40px;
				height: 40px;
				border: 1px solid #fe5f1e;
				border-radius: 50%;
				cursor: pointer;
				transition: all 0.3s ease 0s;
				color: #fe5f1e;
				@media (max-width: 500px) {
					width: 25px;
					height: 25px;
				}
				&:hover {
					background-color: #fe5f1e;
					color: #fff;
				}
			}
		}
	}
}
